<script>
  import axios from 'axios';
  let category = '';
  let joke = '';
  let error = '';

  async function fetchJoke() {
    try {
      const response = await axios.get(`https://joke-api-gamma.vercel.app/api/joke?category=${category}`);
      console.log('API Response:', response.data); // Log the full response to inspect the structure

      if (response.data && response.data.joke) {
        joke = response.data.joke; // Update the joke variable with the response data
        error = '';
      } else {
        joke = '';
        error = 'No joke found. Please try a different category.';
      }
    } catch (err) {
      console.error('Error fetching joke:', err); // Log the error to the console
      joke = '';
      error = 'Error fetching joke. Please try again.';
    }
  }
</script>

<style>
  :global(body) {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    color: #4b0082; /* Dark purple color */
  }

  .container {
    max-width: 600px;
    margin: 2rem auto;
    padding: 2rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }

  h1 {
    color: #4b0082; /* Dark purple color */
    margin-bottom: 1rem;
  }

  input[type="text"] {
    padding: 0.75rem;
    margin-right: 0.5rem;
    border: 2px solid #4b0082;
    border-radius: 4px;
    font-size: 1rem;
  }

  button {
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 4px;
    background-color: #4b0082; /* Dark purple color */
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  button:hover {
    background-color: #3a0066; /* Slightly darker purple */
  }

  .joke {
    margin-top: 1.5rem;
    font-size: 1.2rem;
    line-height: 1.5;
    color: #333;
    border: 1px solid #ddd;
    padding: 1rem;
    border-radius: 4px;
    background-color: #f9f9f9;
  }

  .error {
    color: #d9534f; /* Bootstrap danger color */
    margin-top: 1rem;
    font-size: 1rem;
  }

  .link {
    margin-top: 1.5rem;
    display: block;
    color: #4b0082;
    text-decoration: none;
    font-size: 1rem;
  }

  .link:hover {
    text-decoration: underline;
  }
</style>

<div class="container">
  <h1>Joke Generator</h1>
  <input
    type="text"
    bind:value={category}
    placeholder="Enter category"
  />
  <button on:click={fetchJoke}>Get Joke</button>
  {#if joke}
    <!-- Conditionally display the joke if it exists -->
    <div class="joke">{joke}</div>
  {/if}
  {#if error}
    <!-- Conditionally display the error message if it exists -->
    <div class="error">{error}</div>
  {/if}
  <a class="link" href="/jokes.json" target="_blank">View All Jokes</a>
</div>
